import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AlertService } from '../_services/alert.service';
import { AuthentificationService } from '../_services/authentification.service';
import { Response, Http} from '@angular/http';

@Component({
  selector: 'app-add-task-event',
  templateUrl: './add-task-event.component.html',
  styleUrls: ['./add-task-event.component.css']
})
export class AddTaskComponent implements OnInit {

  model: any = {};
    loading = false;
    event_id: string;
    user_id: any;
    end_dat: any;

    constructor(
        private route: ActivatedRoute,
        private router: Router,
        private authentificationService: AuthentificationService,
        private http:Http,
        private alertService: AlertService) { }

    ngOnInit() {
        this.event_id = this.route.snapshot.params['id'] || '/';
        const user = JSON.parse(localStorage.getItem('currentUser'));
        this.user_id = user.id;
    }

    addtask()
    {
      this.loading = true;
      const date = new Date(this.model.end_date);
      this.end_dat = date.getDate() + '/' + (date.getMonth() + 1) + '/' + date.getFullYear();
      this.prepare_add(this.model.name, this.model.description).subscribe(data => {
        console.log(data);
        window.location.reload();
      });
    }

    prepare_add(name: string, description: string)
    {
      const data = JSON.stringify({user_id: this.user_id, event_id: this.event_id, name: name, description: description, end_date: this.model.end_date});
      return this.http.post("http://163.5.245.49/pro-inno/web/app_dev.php/task/create", data).map((res: Response) => res.json());
    }
}



