import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule, JsonpModule } from '@angular/http';
import { AgmCoreModule } from '@agm/core';
import {HttpClientModule} from '@angular/common/http';

import { AppComponent } from './app.component';
import { TestComponent } from './test/test.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { AccueilComponent } from './accueil/accueil.component';
import { InscriptionComponent } from './inscription/inscription.component';
import { ConnexionComponent } from './connexion/connexion.component';
import { CreateEventComponent } from './createEvent/createEvent.component';
import { InscriptionFormComponent } from './inscription-form/inscription-form.component';
import { InscriptionFormPrestaComponent } from './inscription-form-presta/inscription-form-presta.component';
import { RechercheComponent } from './recherche/recherche.component';
import { RechercheResultComponent } from './recherche-result/recherche-result.component';
import { RechercheBarComponent } from './recherche-bar/recherche-bar.component';
import { GoogleMapComponent } from './google-map/google-map.component';
import { DetailsComponent } from './details/details.component';
import { DetailsUserComponent } from './details_user/details_user.component';
import { DragDropComponent } from './drag-drop/drag-drop.component';
import { DragDropValueComponent } from './drag-drop-value/drag-drop-value.component';
import { DetailsEventComponent } from './details_event/details_event.component';
import { AddTaskComponent } from './add-task-event/add-task-event.component';
import { NotFoundComponent } from './404notfound/404notfound.component';

import { AuthGuard } from './_guards/auth.guard'

import { AlertService } from './_services/alert.service';
import { AuthentificationService } from './_services/authentification.service';
import { UserService } from './_services/user.service';
import { EventService } from './_services/event.service';
import { CookieService } from 'ngx-cookie-service';


import { AlertDirective } from './_directives/alert.directive';
import {DragulaModule} from 'ng2-dragula';
import { UserEventComponent } from './user-event/user-event.component';
import { UserInvitationComponent } from './user-invitation/user-invitation.component';
import {GooglePlaceModule} from 'ng2-google-place-autocomplete';

@NgModule({
  declarations: [
    AppComponent,
    TestComponent,
    HeaderComponent,
    FooterComponent,
    AccueilComponent,
    InscriptionComponent,
    ConnexionComponent,
    InscriptionFormComponent,
    InscriptionFormPrestaComponent,
    DetailsComponent,
    DetailsUserComponent,
    CreateEventComponent,
    RechercheComponent,
    RechercheResultComponent,
    RechercheBarComponent,
    GoogleMapComponent,
    AlertDirective,
    DragDropComponent,
    DragDropValueComponent,
    UserEventComponent,
    DetailsEventComponent,
    UserInvitationComponent,
    AddTaskComponent,
    NotFoundComponent,
  ],
  imports: [
    GooglePlaceModule,
    DragulaModule,
    BrowserModule,
    FormsModule,
    HttpModule,
    HttpClientModule,
    JsonpModule,
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyC1eXKNyCgCfJvqc9ngacuyg0bOi4UQ6r8'
    }),
    RouterModule.forRoot([
    {
      path: '',
      component: AccueilComponent
    },
    {
      path: 'details',
      component: DetailsComponent
    },
    {
      path: 'details_user/:id',
      component: DetailsUserComponent
    },
    {
      path: 'details_event/:id',
      component: DetailsEventComponent
    },
    {
      path: 'inscription',
      component: InscriptionComponent
    },
    {
      path: 'recherche/:first/:second',
      component: RechercheComponent
    },
    {
      path: 'create_event',
      component: CreateEventComponent
    },
    {
      path: 'user_event',
      component: UserEventComponent
    },
    {
      path: 'user_invitation',
      component: UserInvitationComponent
    },
    {
      path: '**',
      component: NotFoundComponent
    }])
  ],
  providers: [
    AuthGuard,
    AlertService,
    AuthentificationService,
    UserService,
    EventService,
    CookieService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



