import { Component, OnInit } from '@angular/core';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import { AuthentificationService } from '../_services/authentification.service'
import {ActivatedRoute, Router} from "@angular/router";
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-recherche-bar',
  templateUrl: './recherche-bar.component.html',
  styleUrls: ['./recherche-bar.component.css']
})
export class RechercheBarComponent {
    zip_code: any;
    adr: any;
    cit: any;
    model: any = {};
    isUser = localStorage.getItem('currentUser');
    user = JSON.parse(this.isUser);
    returnUrl: string;
    constructor(private http: HttpClient, private route: ActivatedRoute, private router: Router,private authentificationService: AuthentificationService,) {
    }

  search() {
      this.router.navigate(['/recherche', this.model.first, this.model.second]);
      location.reload();
  }

    getAddress(place) {
        if (place)
        {
            this.zip_code = place.address_components[6].long_name;
            this.adr = place.address_components[0].long_name + ' ' + place.address_components[1].long_name;
            this.cit = place.address_components[2].long_name;
        }
    }

}



