import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AlertService } from '../_services/alert.service';
import { AuthentificationService } from '../_services/authentification.service';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {log} from "util";
import { UserService } from '../_services/user.service';

@Component({
  selector: 'app-user-invitation',
  templateUrl: './user-invitation.component.html',
  styleUrls: ['./user-invitation.component.css']
})
export class UserInvitationComponent implements OnInit {

  invitations = [];
  user = JSON.parse(localStorage.getItem('currentUser'));
  
  constructor(private userService: UserService,
              private route: ActivatedRoute,
              private router: Router,
              private authentificationService: AuthentificationService,
              private alertService: AlertService,
              private http: HttpClient) { }

  ngOnInit() {
    if (this.user)
      {
        this.userService.getInvitations(this.user.id).subscribe(data => {
          console.log("invitations : ");
          console.log(data['data']);
          this.invitations = data['data'];
        });
      }
  }
  
  inviteResponse(user_event_id, value, index)
  {
    this.userService.inviteResponse(user_event_id, value).subscribe(data => {
      this.invitations.splice(index, 1);
      console.log(data);
    });
  }

}



