import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { AlertService } from '../_services/alert.service';
import { AuthentificationService } from '../_services/authentification.service';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {log} from "util";
import { EventService } from '../_services/event.service';
import 'rxjs/add/operator/map';
import { AgmCoreModule } from '@agm/core';
import { UserService } from '../_services/user.service';


@Component({
    selector: 'app-details_user',
    templateUrl: './details_user.component.html',
    styleUrls: ['./details_user.component.css']
})

export class DetailsUserComponent implements OnInit {
    model: any = {};
    userEvents = [];
    errorAddProvider = false;
    invitationSend = false;
    user = JSON.parse(localStorage.getItem('currentUser'));
    event_id: number;
    details = [];
    returnUrl: string;
    long: number;
    lat: number;
    id: number;

    constructor(private route: ActivatedRoute,
                private router: Router,
                private alertService: AlertService,
                private http: HttpClient,
        private userService: UserService) {
        this.id = parseInt(this.route.snapshot.params['id']);
    }

    AddProviderToEvent()
    {
        //const data = {event_id: this.event_id, is_user_invite: true, provider_id: this.id, owner_id: this.user.id};
        this.userService.addProvider(this.event_id, true, this.id, this.user.id)
        .subscribe(
          (val) => {
              this.errorAddProvider = false;
              this.invitationSend = false;
              if (val['data'] === 'KO')
                 this.errorAddProvider = true;
              else
                this.invitationSend = true;
              console.log("POST call successful value returned in body", val);
          },
          response => {
              console.log("POST call in error", response);
          },
          () => {
              console.log("The POST observable is now completed.");
          });
    }

    getUserEvent()
    {
      const user = JSON.parse(localStorage.getItem('currentUser'));
      //const data = JSON.stringify({user_id: user.id});
      return this.userService.getUserEvents(user.id);
    }

    getData()
    {
      //const data = JSON.stringify({user_id: this.id});
      return this.userService.getUserDetail(this.id);
    }

    ngOnInit() {
        this.getData()
        .subscribe(
        (val) => {
            console.log("POST call successful value returned in body", val['data']);
            this.details = val['data'];
            this.long = this.details['logitude'];
            this.lat = this.details['latitude'];
        },
        response => {
            console.log("POST call in error", response);
        },
        () => {
            console.log("The POST observable is now completed.");
        });

         this.getUserEvent()
           .subscribe(
           (val) => {
               console.log("POST USER EVENT call successful value returned in body", val['data']);
               this.userEvents = val['data'];
               this.event_id = this.userEvents[0].id;
           },
           response => {
               console.log("POST call in error", response);
           },
           () => {
               console.log("The POST observable is now completed.");
           });
          this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    }
}



