import { Injectable } from '@angular/core';
import {Http, Headers, Response, RequestOptions} from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { UserService } from './user.service';
import 'rxjs/add/operator/map'
import 'rxjs/add/operator/catch';

@Injectable()
export class AuthentificationService {
    user_object: any = {};

    constructor(private http: Http) { }

    login(username: string, password: string) {
        //console.log({ username, password });
        //console.log(typeof username, typeof password);
        return this.http.post('http://163.5.245.49/pro-inno/web/app_dev.php/user/login', { username, password })
            .map((response: Response) => {
                // login successful if there's a jwt token in the response
                //console.log(response);
                const user = response.json();
                console.log(user);
                if (user) {
                    console.log(1);
                    // store user details and jwt token in local storage to keep user logged in between page refreshes
                    const storedUser = localStorage.setItem('currentUser', JSON.stringify(user.data));
                    console.log(2);
                    this.user_object = JSON.parse(localStorage.getItem('currentUser'));
                    console.log(this.user_object);
                    window.location.reload();
                    //console.log("test authentification service",typeof JSON.parse(localStorage.getItem('currentUser')) , JSON.parse(localStorage.getItem('currentUser')));
                }
            });
    }

    getUser() {
        return this.user_object;
    }


    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
    }
}


